<template>
  <div class="home">
    <img alt="Vue logo" src="../../assets/logo.png">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div v-for="property in properties" :key='property.id' class="property">
        <p><b>Name:</b> {{ property.name }}</p>
        <p><b>Price:</b> ${{ property.min_rate }}</p>
        <p><b>Number of Suites:</b> {{ property.suite_count }}</p>
        <p><b>Status:</b> {{ property.status }}</p>

    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'home',
  data () {
    return {
      properties: {}
    }
  },
  components: {
    HelloWorld
  },
  async mounted () {
    this.properties = await this.$get_properties()
  }
}

</script>
